.calendar-wrapper{width:100%;position:relative;margin-left:auto;margin-right:auto;color:#616161;font-size:1em;overflow:hidden;box-shadow:0px 0px 50px #888888}@media (min-width: 576px){.calendar-wrapper{width:100%}}@media (min-width: 768px){.calendar-wrapper{width:90%}}@media (min-width: 992px){.calendar-wrapper{width:60%}}@media (min-width: 1200px){.calendar-wrapper{width:40%}}@media (min-width: 1300px){.calendar-wrapper{width:30%}}.calendar-wrapper:before{content:"";display:block;padding-top:120%}

.icon{font-family:'Material Icons', serif;font-style:normal;display:inline-block;vertical-align:middle;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;-webkit-font-feature-settings:'liga';font-feature-settings:'liga'}:root{--global-color: #616161;--header-color: #ffffff;--background-color-body: #ffffff;--background-color-light-4: #39f10b;--background-color-light-3: #0bca3b;--background-color-main: #265c1e;--background-color-dark: #10531b;--animation-delay: 0.5s}body{font-family:'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;font-weight:300;font-size:28px}.calendar-content{position:absolute;top:0;left:0;bottom:0;right:0;background-color:#10531b;background-color:var(--background-color-dark)}.main-calendar{height:100%;display:flex;flex-wrap:wrap}.main-calendar .calendar-header{display:flex;flex-wrap:wrap;width:100%;height:30%;color:#ffffff;color:var(--header-color)}.main-calendar .calendar-header .title-header{width:100%;height:70%;white-space:nowrap;font-size:1.2em;background-color:#10531b;background-color:var(--background-color-dark)}.main-calendar .calendar-header .title-header .header-text{flex:5 1;display:flex;height:100%}.main-calendar .calendar-header .title-header .header-text .today-label{flex:1 1;font-size:0.8em}.main-calendar .calendar-header .title-header .header-text .today-label:hover{cursor:pointer;color:--background-color-dark;background-color:#ffffff;background-color:var(--header-color)}.main-calendar .calendar-header .title-header .header-text .month-label{flex:3 1}.main-calendar .calendar-header .days-header{width:100%;height:30%;background-color:#265c1e;background-color:var(--background-color-main)}.main-calendar .calendar-header .button-container{width:100%;height:30%;background-color:#265c1e;background-color:var(--background-color-main)}.main-calendar .calendar-header .button-container .event-button{flex-grow:1;display:flex;height:100%;align-items:center;justify-content:center}.main-calendar .calendar-header .button-container .event-button:hover{background-color:#fff;color:#265c1e;color:var(--background-color-main)}.main-calendar .days-container{width:100%;height:70%;background:#ffffff;background:var(--background-color-body)}.main-calendar .days-container .week{height:15%}.main-calendar .day-events{position:relative;width:100%;height:70%;background-color:#ffffff;background-color:var(--background-color-body);font-size:1.2em}.main-calendar .day-events .event-container{width:100px;height:auto;text-align:center;display:flex;float:left}.main-calendar .day-events .event-container:hover{cursor:pointer}.main-calendar .day-events .event-container .animated-time{width:30%}.main-calendar .day-events .event-container .animated-title{width:70%}.main-calendar .day-events .event-container .event-attribute{height:100%;display:flex;justify-content:center;align-items:center;overflow:hidden;box-sizing:border-box;padding:5px}.row{display:flex;justify-content:center;align-items:center}.box{flex-grow:1;display:flex;justify-content:center;align-items:center;flex:1 1;height:100%;transition:all 0.4s ease-in-out 0s}.box.arrow:hover{background-color:white;cursor:pointer;color:#10531b;color:var(--background-color-dark);transition:all 0.2s ease-in-out 0s}.day{display:flex;justify-content:center;align-items:center;flex:1 1;height:100%}.day .day-number{width:80%;height:90%;display:flex;justify-content:center;align-items:center;border:1px solid #ffffff;border:1px solid var(--background-color-body);box-sizing:border-box;border-radius:50%}.day:hover .day-number{cursor:default;background-color:#0bca3b;background-color:var(--background-color-light-3);color:#ffffff;color:var(--background-color-body);transition:background-color 0.2s ease-in-out 0s}.day.today .day-number{border:1px solid #0bca3b;border:1px solid var(--background-color-light-3)}.day.has-events .day-number{color:#10531b;color:var(--background-color-dark);font-weight:bold}.day.different-month{opacity:0.5}.button-month{width:80px;color:white;font-size:18px;border-radius:4px;background:transparent}.button-week{width:80px;color:white;font-size:18px;border-radius:4px;background:transparent}.card{padding:20px;width:80px}img{width:60px;height:50px;border:2px solid #e1e1e1;border-radius:50%}table,table tr td{width:30px;margin:0 auto;text-align:lt}.person-name{font-weight:bold;font-size:18px}.leave-reason{font-size:13px}

